import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Nav, NavDropdown, Button, Container, Col, Row } from 'react-bootstrap';

import BarChart from './BarChart';
import Dropdown from './Dropdown';
import PieChart from './PieChart';
import Sidebar from './Sidebar';
import Top from './Top';

function Main() {


    const [any, setAny] = useState(2021);
    const [dadesEvolucio, setDadesEvolucio] = useState();
    const [dades, setDades] = useState();
    const [dadesNostre, setDadesNostre] = useState();
    const [pagina, setPagina] = useState("general");

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    let revista = Number(any) + 1;

    useEffect(() => {

        axios.get("https://raw.githubusercontent.com/Ponsoda/alcoi-m-c-dades/main/associacioSantJordi/dadesHistoriques.json")
            .then(res => { setDadesEvolucio(res.data.associats); })

        axios.get("https://raw.githubusercontent.com/Ponsoda/alcoi-m-c-dades/main/associacioSantJordi/revista" + revista + "/dades" + any + ".json")
            .then(res => { setDades(res.data); })
    }, [])

    useEffect(() => {
        axios.get("https://raw.githubusercontent.com/Ponsoda/alcoi-m-c-dades/main/associacioSantJordi/revista" + revista + "/dades" + any + ".json")
            .then(res => { setDades(res.data); })
    }, [any])

    useEffect(() => {
        axios.get("https://raw.githubusercontent.com/Ponsoda/alcoi-m-c-dades/main/elnostre/guia2022/dades2022.json")
            .then(res => { setDadesNostre(res.data);})
    }, [])

    if (pagina === "general") {

        return (
            <Container>
                <Top
                    any={any}
                    setAny={setAny}
                    show={show}
                    handleShow={handleShow}
                    handleClose={handleClose}
                    setPagina={setPagina}
                    dadesEvolucio={dadesEvolucio}
                />
                <Container>
                    <Row>
                        <Col>
                            <PieChart
                                dadaHomes={(dades === undefined ? "" : dades.associats.homes)}
                                dadaDones={(dades === undefined ? "" : dades.associats.dones)}
                                titol={"Associats"}
                            />
                        </Col>
                        <Col>
                            <PieChart
                                dadaHomes={(dades === undefined ? "" : dades.junta.homes)}
                                dadaDones={(dades === undefined ? "" : dades.junta.dones)}
                                titol={"Junta directiva"}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <PieChart
                                dadaHomes={(dades === undefined ? "" : dades.primersTrons.homes)}
                                dadaDones={(dades === undefined ? "" : dades.primersTrons.dones)}
                                titol={"Primers Trons"}
                            />
                        </Col>
                        <Col>
                            <PieChart
                                dadaHomes={(dades === undefined ? "" : dades.majorals.homes)}
                                dadaDones={(dades === undefined ? "" : dades.majorals.dones)}
                                titol={"Majorals"}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <PieChart
                                dadaHomes={(dades === undefined ? "" : dades.cuadroHonor.homes)}
                                dadaDones={(dades === undefined ? "" : dades.cuadroHonor.dones)}
                                titol={"Cuadro d'Honor"}
                            />
                        </Col>
                        <Col>
                            <PieChart
                                dadaHomes={(dades === undefined ? "" : dades.personatgesFesters.homes)}
                                dadaDones={(dades === undefined ? "" : dades.personatgesFesters.dones)}
                                titol={"Personatges Festers"}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col md={{ span: 6, offset: 3 }} style={{ minHeight: "25vh" }}>
                            <BarChart
                                dadesEvolucio={dadesEvolucio}
                            />
                        </Col>
                    </Row>
                </Container>
            </Container>
        )
    } else if (pagina === "perFila") {

        return (
            <Container>
                <Top
                    any={2022}
                    setAny={setAny}
                    show={show}
                    handleShow={handleShow}
                    handleClose={handleClose}
                    setPagina={setPagina}
                    dadesEvolucio={dadesEvolucio}
                    dropdownBoolean={false}
                />
                <Container>
                <h2 style={{marginTop: "10px"}}>Filaes del bando Moro</h2>
                    <Row>
                        <Col xs={3}>
                            <PieChart
                                dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.llana.homes)}
                                dadaDones={(dadesNostre === undefined ? "" : dadesNostre.llana.dones)}
                                titol={"Llana"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.judios.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.judios.dones)}
                                titol={"Judios"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.domingoMiques.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.domingoMiques.dones)}
                                titol={"Domingo Miguqes"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.chano.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.chano.dones)}
                                titol={"Chano"}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.verds.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.verds.dones)}
                                titol={"Verdes"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.magenta.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.magenta.dones)}
                                titol={"Magenta"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.cordon.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.cordon.dones)}
                                titol={"Cordn"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.ligeros.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.ligeros.dones)}
                                titol={"Ligeros"}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.mudejares.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.mudejares.dones)}
                                titol={"Mudjares"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.abencerrajes.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.abencerrajes.dones)}
                                titol={"Abencerrajes"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.marrakesch.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.marrakesch.dones)}
                                titol={"Marrakesch"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.realistes.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.realistes.dones)}
                                titol={"Realistes"}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.berberiscos.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.berberiscos.dones)}
                                titol={"Berberiscos"}
                            />
                        </Col>
                        <Col xs={3}>
                            <PieChart
                            dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.benimerines.homes)}
                            dadaDones={(dadesNostre === undefined ? "" : dadesNostre.benimerines.dones)}
                                titol={"Benimerines"}
                            />
                        </Col>
                    </Row>
                    <h2 style={{marginTop: "10px"}}>Filaes del bando Cristiano</h2>
                    <Row>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.andaluces.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.andaluces.dones)}
                            titol={"Andaluces"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.asturianos.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.asturianos.dones)}
                            titol={"Asturianos"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.cides.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.cides.dones)}
                            titol={"Cides"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.labradores.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.labradores.dones)}
                            titol={"Labradores"}
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.gusmans.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.gusmans.dones)}
                            titol={"Guzmanes"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.vascos.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.vascos.dones)}
                            titol={"Vascos"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.mozarabes.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.mozarabes.dones)}
                            titol={"Mozrabes"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.almogavares.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.almogavares.dones)}
                            titol={"Almogvares"}
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.navarros.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.navarros.dones)}
                            titol={"Navarros"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.tomasinas.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.tomasinas.dones)}
                            titol={"Tomasinas"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.muntanyesos.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.muntanyesos.dones)}
                            titol={"Muntanyesos"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.cruzados.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.cruzados.dones)}
                            titol={"Cruzados"}
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.alcodianos.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.alcodianos.dones)}
                            titol={"Alcodians"}
                        />
                    </Col>
                    <Col xs={3}>
                        <PieChart
                        dadaHomes={(dadesNostre === undefined ? "" : dadesNostre.aragonesos.homes)}
                        dadaDones={(dadesNostre === undefined ? "" : dadesNostre.aragonesos.dones)}
                            titol={"Aragonesos"}
                        />
                    </Col>
                </Row>
                </Container>
            </Container>
        )

    }
}

export default Main;