import { Offcanvas } from 'react-bootstrap';

const Sidebar = ({ show, handleClose }) => {
    return (
        <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
            <Offcanvas.Title>Informaci</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
            Dades sobre la participaci de la dona a les festes de Moros i Cristians d'Alcoi extretes a partir de la informaci accessible de l'Associaci de Sant Jordi https://www.asjordi.org/.
        <br/>
        <br/>
        El apartat "Per fil" fa menci a les dades de la directiva.
        <br/>
        <br/>
        Fe d'errates: la majoria de dades s'han recollit de forma manual, pel que pot haver-hi alguna errata.
            </Offcanvas.Body>
    </Offcanvas>
    )
}

export default Sidebar;