import { Nav, NavDropdown } from "react-bootstrap";

import Dropdown from "./Dropdown";
import Sidebar from "./Sidebar";

const Top = ({any, setAny, show, handleShow, handleClose, setPagina, dadesEvolucio, dropdownBoolean=true}) => {

    return (

        <Nav className="Nav" onSelect={(key) => setAny(key)}>
            <Nav.Item className="nav-btn" onClick={handleShow}>
                <Nav.Link className="Nav-Title">
                    <b> Informaci </b>
                </Nav.Link>
            </Nav.Item>
            <Sidebar
                show={show}
                handleClose={handleClose}
            />
            <Nav.Item className="nav-btn" onClick={() => setPagina("general")}>
                <Nav.Link className="Nav-Title">
                    <b> Dades Generals </b>
                </Nav.Link>
            </Nav.Item>
            <Nav.Item className="nav-btn" onClick={() => setPagina("perFila")}>
                <Nav.Link className="Nav-Title">
                    <b> Per Fil </b>
                </Nav.Link>
            </Nav.Item>
            {dropdownBoolean ?  
            <NavDropdown className="nav-btn" title={<span className="Nav-Title">Selecciona Any</span>} id="nav-dropdown">
                <Dropdown
                    dadesEvolucio={dadesEvolucio}
                />
            </NavDropdown>
            : ""}
            <Nav.Item>
                <Nav.Link eventKey="disabled" disabled>
                    <b> {any} </b>
                </Nav.Link>
            </Nav.Item>
        </Nav>

    )
}

export default Top;